.contactsBlock {
    height: 50vh;
    background-color: #fcdd88;
}

.contactsContainer {
    flex-direction: column;
    justify-content: space-between;
}

.contactsTitle {
    background-color: #e5ddd2;
}

.formBox {
    border: 1px solid;
    width: 450px;
    height: 150px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-wrap: wrap;
}

.input {
    height: 15%;
}

.texterea {
    height: 50%;
}

.button {
    width: 100px;
    background-color: #e5ddd2;
}