.project {
    border: 1px solid;
    width: 450px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-wrap: wrap;

}

.ProjectIcon {
    height: 100px;
    border: 1px solid;
    background-color: #d4bf38;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.infoProject {
    width: 100%;
    background-color: #fdfcfc;
    border: 1px solid;
    text-align: start;
}

.titleProject {
    background-color: #e5e5e5;


}

.textProject {
    background-color: #e5e5e5;

}